import state from "../core/state";
import storage from "../util/storage";
import dataJS from "../util/data";
import Listener from "./listener";
import moment from "moment/src/moment";
import momentTimezone from "moment-timezone";
import countryTimezone from "countries-and-timezones";
import jwtDecode from "jwt-decode";
const initiateURL = "https://ninjachat.${region}.500apps.com/ninjachat/initiate";
const messageURL = "https://ninjachat.${region}.500apps.com/ninjachat/message";
const brodadcastURL = "https://ninjachat.${region}.500apps.com/ninjachat/broadcast/message";
const closeURL = "https://ninjachat.${region}.500apps.com/ninjachat/client/close";

class Chat {
  constructor() {
    // Create storage instance
    this.listener = new Listener();

    // Get visitor country timezone
    this.countryByTimezone = countryTimezone.getCountryForTimezone(momentTimezone.tz.guess());
    this.activeConversation = [];
    this.storage = new storage();
    // To make chat active after refresh based on agent details
    var refreshFlag = this.storage.getSessionStorage("chat");
    if (refreshFlag) {
      this.storage.setSessionStorage("chat", "Active");
      this.listener.listen(window._NC.State.getters.getAgentDetails);
    }
  }

  /**
   * Send message to agent
   * @param {String} visitorMessage Which is given by visitor
   */
  sendMessage(visitorMessage) {
    var visitorResponse = {};
    visitorResponse.message = visitorMessage;
    visitorResponse.subject = "ninjachat_new_message";
    // To save data in vuex
    this.saveInState(visitorResponse);
  }

  // send data into vuex and array
  saveInState(visitorResponse) {
    // Set time
    var date = new Date();
    // Format current date like MMM DD,YYYY
    visitorResponse.time = moment(date).format("MMM DD,YYYY hh:mm A");
    // To save data in vuex
    state.commit("setMessages", visitorResponse);

    // Object push into array of JSON
    this.activeConversation.push(visitorResponse);

    // Send to agent
    Object.keys(window._NC.State.getters.getNinjaJWT).length ? this.sendMessageToAgent(visitorResponse) : this.initiateChat(visitorResponse);
  }

  /**
   * Initiate chat by visitor & send to agent portal
   * @param {Object} messageObj Which is given by visitor
   */
  async initiateChat(messageObj) {
    var visitorResponse = {};
    var data = new dataJS(initiateURL);
    // Get visitor information county,IP etc.. from local storage
    var visitorInfo = await this.storage.getLocalStorage(`visitorInfo`);
    visitorResponse.api_key = window.api_key;
    visitorResponse.nVersion = visitorInfo.nVersion;
    visitorResponse.nAgent = visitorInfo.nAgent;
    visitorResponse.browserName = visitorInfo.browser;
    visitorResponse.majorVersion = visitorInfo.majorVersion;
    visitorResponse.browser_version = visitorInfo.version;
    visitorResponse.operating_system = visitorInfo.OS;
    visitorResponse.device_type = visitorInfo.deviceType;
    visitorResponse.from_url = window.location.href;
    visitorResponse.department_id = window.departmentID;
    messageObj.subject = "new_session";
    visitorResponse.message = messageObj;
    visitorResponse.resolution = `${visitorInfo.deviceWidth}x${visitorInfo.deviceHeight}`;
    visitorResponse.visitor_id = window.visitorID;
    visitorResponse.tz = this.countryByTimezone.name;
    // As of now we put as static
    visitorResponse.name = "500apps";
    visitorResponse.email = "agilecrm@gmail.com";
    let ninjaJWT = await data.add(visitorResponse, true);
    await this.getJWTDecoded(ninjaJWT);
    this.storage.setSessionStorage("chat", "Active");
    // To call ably service
    if (Object.keys(ninjaJWT).length) this.listener.listen(ninjaJWT.agent_details);
  }

  /**
   * Send to agent portal
   * @param {Object} messageObj Which is given by visitor
   */
  async sendMessageToAgent(messageObj) {
    // check in state whether the chat is already started or not
    if (!window._NC.State.getters.getActiveChannel) return this.broadcastNewSession(messageObj);
    var data = new dataJS(messageURL);
    let activeChannel = window._NC.State.getters.getActiveChannel.split("-");
    let result = {
      agent_id: activeChannel[0],
      id: activeChannel[1],
      channel: activeChannel[2],
      type: "chat",
      message: messageObj,
    };
    result.domain_id = window._NC.State.getters.getNinjaJWT.domain_id;
    result.session_id = window._NC.State.getters.getNinjaJWT.session_id;
    await data.add({ ninja_jwt: result }, true);
  }

  /**
   * Brodcast the message if agent not accepted
   * @param {Object} messageObj message which is give by visitor
   */
  async broadcastNewSession(messageObj) {
    var data = new dataJS(brodadcastURL);
    let payload = window._NC.State.getters.getNinjaJWT;
    payload.message = messageObj;
    await data.add({ ninja_jwt: payload }, true);
  }

  /**
   * Decode visitor JWT
   * @param {Object} ninjaJWT ninjaJWT JWT by visitor
   */
  async getJWTDecoded(ninjaJWT) {
    if (!ninjaJWT.ninja_jwt) return;
    // To save agent details in vuex when browser is refreshed
    window._NC.State.commit("setAgentDetails", ninjaJWT.agent_details);
    ninjaJWT = await jwtDecode(ninjaJWT.ninja_jwt);
    window._NC.State.commit("setNinjaJWT", ninjaJWT);
    return ninjaJWT;
  }

  /**
   * Close agent
   * @param {Object} messageObj message which is give by visitor
   */
  async close(messageObj) {
    var data = new dataJS(closeURL);
    let payload = window._NC.State.getters.getNinjaJWT;
    payload.message = {};
    payload.message.message = "HII";
    payload.message.subject = messageObj.subject;
    // Set time
    var date = new Date();
    // Format current date like MMM DD,YYYY
    payload.message.time = moment(date).format("MMM DD,YYYY hh:mm A");
    let activeChannel = window._NC.State.getters.getActiveChannel.split("-");
    (payload.agent_id = activeChannel[0]), (payload.id = activeChannel[1]), (payload.channel = activeChannel[2]), await data.add({ ninja_jwt: payload, chat_transcript: window._NC.State.getters.getCurrentMessages, type: "chat" }, true);
  }
}
export default Chat;



