class NotificationService {
  constructor() {}

  // Register to notification service
  clientRegister(module, clientKey, clientId) {
    window[module].init(clientKey, clientId);
  }
  /**
   * subscription of channels
   * @param {String} module // Messaging service module instance
   * @param {String} domain
   * @param {String} user_id
   * @param {String} channel
   */
  // eslint-disable-next-line no-unused-vars
  subscribe(module, domain, user_id, channel, type, callback) {
    let subscribe_key = this.get_subscribe_key(domain, user_id, channel);
    if (!this.isSubscribed(module, subscribe_key, type)) {
      window[module].register(`${subscribe_key}`, type, (message) => {
        try {
          callback(message.data); // Call callback method after receiving message
          // eslint-disable-next-line no-empty
        } catch (err) {}
      });
    }
  }
  // Check is channel already subscribed or not
  isSubscribed(module, subscribe_key, type) {
    let is_registered = window[module].getChannel(subscribe_key, type, "register");
    return is_registered;
  }
  get_subscribe_key(domain, user_id, channel) {
    return `${domain}-${user_id}${channel ? `-${channel}` : ""}`;
  }
}
export default new NotificationService();



