<template>
  <div>
    <div id="ninjachat" class="body-scroll" :style="UIPrefs.font_family.css">
      <div class="ninjachat-welcome-box">
        <div class="ninjachat-welcome-bg">
          <p class="ninjachat-welcome-message">
            {{ UIPrefs.chat_header.attr.widget_welcome_msg }}
            <b-img :src="UIPrefs.chat_header.attr.welcome_img" width="20" height="20"></b-img>
          </p>
        </div>
      </div>
      <Message v-for="(list, index) in conversations" :key="index" :conversations="list"></Message>
    </div>
  </div>
</template>
<script>
  import Message from "./Message.vue";
  export default {
    components: {
      Message,
    },
    data() {
      return {
        UIPrefs: window._NC.UIPrefs.prefs,
      };
    },
    mounted() {
      // Automatically scroll down in onload
      this.scrollToEnd();
    },
    computed: {
      conversations() {
        // Set tone while agent replay
        if (window.refresh_loading) {
          window._NC.UIPrefs.play(this.UIPrefs.chat.sound.playMessageIn);
        }
        // Automatically scroll down in onload
        this.scrollToEnd();
        // Get data from mutation state through veux
        return window._NC.State.getters.getCurrentMessages;
      },
    },
    methods: {
      // Automatically scroll down while updating conversation
      scrollToEnd() {
        setTimeout(() => {
          const container = document.getElementsByClassName("chat-body-height ninjachat-bg-img-section")[0];
          if (container) container.scrollTop = container.scrollHeight;
        }, 0);
      },
    },
  };
</script>



