import Storage from "../util/storage";
import state from "../core/state";
import session from "../util/state/session";

class Conversation {
  constructor() {
    this.storage = new Storage();
  }

  // Get all conversations
  getAllConversations() {
    return state.getters.getCurrentMessages;
  }

  // Get active conversation from local storage
  getCurrentActiveConversationID() {
    if (!this.storage.getLocalStorage("conversationID")) {
      var conversationID = this.startNewActiveConversation();
      this.storage.setLocalStorage("conversationID", conversationID);
      return conversationID;
    } else return this.storage.getLocalStorage("conversationID");
  }

  // set active conversation to vuex
  setActiveConversationID(conversationID) {
    state.commit("setActiveConversationID", conversationID);
  }

  // Generate active Conversation
  startNewActiveConversation() {
    var sessionInfo = new session();
    var conversationID = sessionInfo.getConversationId();
    this.setActiveConversationID(conversationID);
    return conversationID;
  }
}
export default Conversation;



