<template>
  <div class="ninjachat-footer-500app" :style="UIPrefs.font_family.css">
    <div class="ninjachat-items-center mb-0" v-if="!conversationFooter">Ninjachat by<b-link class="ninjachat-500app-link" rel="nofollow" @click="redirectTo500apps()">500apps</b-link></div>
    <div class="ninjachat-items-center mb-0 pt-1" v-else>Ninjachat by<b-link class="ninjachat-500app-link" rel="nofollow" @click="redirectTo500apps()">500apps</b-link></div>
  </div>
</template>
<script>
  export default {
    props: {
      conversationFooter: Boolean,
    },
    data() {
      return {
        UIPrefs: window._NC.UIPrefs.prefs,
      };
    },
    methods: {
      redirectTo500apps() {
        window.open("https://500apps.com");
      },
    },
  };
</script>



