import Util from "./util/main";
import State from "./core/state";
import Chat from "./chat/main";
import UIPrefs from "../css/prefs";

class ninjachat {
  constructor() {
    this.Util = Util;
    this.Chat = Chat;
    this.State = State;
    this.UIPrefs = new UIPrefs();
    // Create storage object
    this.storage = new this.Util.storage();
    this.storage.setSessionStorage("chat", "Inactive");
  }

  /**
   * Entry point of script which given by user
   * @param {Number} apiKey  Invoke API KEY from website
   * @param {Number} departmentID   which is given by user
   * @param {String} region   which is given by user
   */
  async init(apiKey, departmentID, region) {
    window.apiKey = apiKey;
    window.departmentID = departmentID;
    window.region = region;
    window.refresh_loading = false;

    // Create session instance
    var session = new this.Util.session();

    // Create storage instance
    var listener = new this.Chat.listener();

    // Load the notification script
    await this.loadScript(listener);

    // Get prefs based on domain ID
    await window._NC.UIPrefs.getPrefs();

    // Inject respective theme css based on DB prefs
    window._NC.UIPrefs.loadCSS(`https://widgets.500apps.com/ninjachat/CSS/${window._NC.UIPrefs.prefs.widget}.css`, `500apps-${window._NC.UIPrefs.prefs.widget}`);

    // Create conversation instance
    var conversation = new this.Chat.conversations();

    // Generate active conversation ID
    conversation.getCurrentActiveConversationID();

    // Generate visitor information county,IP etc..
    var visitorInfo = session.getVisitorInfo();

    // Generate visitor ID with respective website
    session.getVisitorID((id) => {
      window.visitorID = id;
    });
  }

  // Load the notification script
  async loadScript(Listener) {
    let isLoaded = await this.loadWithPromise(`https://infinity.500apps.com/js/notification-service/notifications.js`);
    if (isLoaded) Listener.registerToNotificationService(window.departmentID.toString());
  }

  // Inject notification dist link in head tag
  loadWithPromise(src, id) {
    // eslint-disable-next-line no-unused-vars
    return new Promise((resolve, reject) => {
      if (document.querySelector('script[src="' + src + '"]')) return reject("fail");
      var script = document.createElement("script");
      script.src = src;
      // Append id to the tag when passed
      if (id) script.id = id;
      document.head.appendChild(script);
      script.addEventListener("load", () => {
        resolve("done");
      });
    });
  }
}
export default ninjachat;



