<template>
  <div>
    <div :class="`ninjachat-${UIPrefs.widget}-comment-section-chat`" :style="UIPrefs.font_family.css">
      <div v-if="UIPrefs.widget == 'social'">
        <div v-if="conversations.to" :class="`ninjachat-${UIPrefs.widget}-comment-section-replay`">
          <div :class="`ninjachat-bg-reply-${UIPrefs.widget}`">
            <div class="profile-flex-end">
              <b-img src="https://infinity.500apps.com/avatars/avatar-57.png" class="social-avatar rounded-circle mr-2" width="20" height="20"></b-img>
              <div class="ninjachat-small-2" v-if="conversations.time">
                <vue-moments-ago prefix="" suffix="ago" :date="conversations.time" lang="en" />
              </div>
            </div>
            <div v-html="conversations.to" class="margin-zero"></div>
          </div>
        </div>
        <div v-if="conversations.message" :class="`ninjachat-${UIPrefs.widget}-message-box`">
          <div :class="`ninjachat-bg-chat-${UIPrefs.widget}`">
            <div class="profile-flex">
              <div class="ninjachat-small-2" v-if="conversations.time"><vue-moments-ago prefix="" suffix="ago" :date="conversations.time" lang="en" /></div>
              <div class="profile-flex">
                <div class="ninjachat-social-avatar-name">{{ UIPrefs.chat_header.attr.widget_name }}</div>
                <div><b-img :src="UIPrefs.chat_header.attr.gravatar" class="social-avatar rounded-circle mr-2" width="20"></b-img></div>
              </div>
            </div>
            <div v-html="conversations.message" class="margin-zero"></div>
          </div>
        </div>
      </div>
      <div v-else>
        <div v-if="conversations.to" :class="`ninjachat-${UIPrefs.widget}-comment-section-replay`">
          <div :class="`ninjachat-bg-reply-${UIPrefs.widget}`">
            <div v-html="conversations.to" class="margin-zero"></div>
          </div>
          <div class="ninjachat-small-2" v-if="conversations.time"><vue-moments-ago prefix="" suffix="ago" :date="conversations.time" lang="en" /></div>
        </div>
        <div v-if="conversations.message" :class="`ninjachat-${UIPrefs.widget}-message-box`">
          <div class="ninjachat-avatar-flex">
            <div v-if="UIPrefs.widget == 'ultra'" class="align-self-end">
              <b-img :src="UIPrefs.chat_header.attr.gravatar" class="rounded-circle mr-2 rounded-avatar" width="24"></b-img>
            </div>
            <div :class="`ninjachat-bg-chat-${UIPrefs.widget}`">
              <div v-html="conversations.message" class="margin-zero"></div>
            </div>
          </div>
          <div class="ninjachat-small-2" v-if="conversations.time"><vue-moments-ago prefix="" suffix="ago" :date="conversations.time" lang="en" /></div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
  import VueMomentsAgo from "vue-moments-ago";

  export default {
    components: { VueMomentsAgo },
    props: ["conversations"],
    data() {
      return {
        UIPrefs: window._NC.UIPrefs.prefs,
      };
    },
    mounted() {
      // Automatically scroll down in onload
      window.refresh_loading = true;
      this.scrollToEnd();
    },
    methods: {
      // Automatically scroll down while updating conversation
      scrollToEnd() {
        setTimeout(() => {
          const container = document.getElementsByClassName("chat-body-height ninjachat-bg-img-section")[0];
          if (container) container.scrollTop = container.scrollHeight;
        }, 0);
      },
    },
  };
</script>



