import session from "../util/state/session";
import send from "./send";
import NotificationService from "../notification/ably-service";

class Listener {
  constructor() {
    this.sessionInfo = new session();
  }

  // Register to notification service
  registerToNotificationService(clientID) {
    NotificationService.clientRegister("messaging", this.sessionInfo.getAbly(), clientID);
  }

  // Subscribe channel to messaging service
  async listen(channels) {
    if (!channels) return;
    channels.forEach(async (channel) => {
      await NotificationService.subscribe("messaging", channel.agent_id, channel.id, channel.channel, "", (message) => {
        /**
         * Listen message from agent through ably service
         * @param {Object} message Which is send by visitor
         */
        this.execute(message);
      });
    });
  }

  /**
   * Execute agent activities
   * @param {Object} message Which is send by visitor
   * @param {String} type
   */
  execute(message) {
    let response;
    let type = message.subject;
    switch (type) {
      case "chat_assigned":
        window._NC.State.commit("setMessages", { to: message.message, time: message.time });
        window._NC.State.commit("setActiveChannel", message.channel_id);
        break;
      case "close_chat":
        new window._NC.Chat.send().close(message);
        break;
      case "Chat closed":
        window._NC.State.commit("setMessages", { to: message.message, time: message.time });
        break;
      default:
        return new Promise((resolve) => {
          resolve();
        });
    }

    return response;
  }

  /**
   * Send message after delay
   */
  waitSendMessage() {
    var milliSec = 120;
    return new Promise((resolve, reject) => {
      // Usage!
      this.sleep(milliSec, resolve).then(() => {
        resolve("success");
        /**
         * Listen message from agent through ably service
         * @param {Object} message Which is send by visitor
         */
        window.messaging({ message: "Are u their?", subject: "chat_assigned" });
      });
    });
  }
}
export default Listener;



