<template>
  <div class="ninjachat-footer-section">
    <div class="ninjachat-text-area-icons">
      <div class="ninjachat-footer-icons">
        <b-form @submit.prevent="submit">
          <b-form-textarea
            class="pr-3 bg-input-color"
            id="editor"
            v-model="content"
            type="text"
            size="lg"
            oninvalid="Enter at least 5 characters"
            :state="content.length >= 10"
            onchange="setCustomValidity('')"
            placeholder="Write a reply..."
            @blur="submit()"
            v-on:keyup.enter="$event.target.blur()"
            aria-describedby="input-live-help input-url-feedback"
          />
          <button class="ninjachat-send-icon" type="submit" v-if="add_button">
            <svg class="ninjachat-send-img" :disabled="button_disable || !content || content.length > max_length" viewBox="0 0 29 29" fill="none" xmlns="http://www.w3.org/2000/svg">
              <path d="M0 0L29 14.5L0 29L8.5 14.5L0 0Z" fill="#4E71EE" />
            </svg>
          </button>
        </b-form>
      </div>
    </div>
  </div>
</template>
<script>
  export default {
    props: {
      enableChat: Boolean,
      max_length: {
        type: Number,
        default: 1000, // Text validation
      },
      link_placeholder: {
        type: String,
        default: "https://500apps.com",
      },
    },
    data() {
      return {
        UIPrefs: window._NC.UIPrefs.prefs,
        placeholder: "Message",
        content: "",
        button_disable: false,
        add_button: true,
      };
    },
    methods: {
      /**
       * Event fires when user send message
       * @param {Object} message  Invoke message which is given by user
       */
      submit() {
        window.refresh_loading = true;
        if (!this.content) return;
        // Create agent instance
        var sendInfo = new window._NC.Chat.send();
        sendInfo.sendMessage(this.content);
        // Set tone while user replay
        window._NC.UIPrefs.play(this.UIPrefs.chat.sound.playMessageOut);
        // To Remove existing text content in Quill editor
        if (document.getElementsByClassName("editor")) document.getElementById("editor").value = null;

        if (this.enableChat)
          this.$router.push({
            name: "chat",
            params: { active: true },
          });
      },
    },
  };
</script>
<style>
  .ninjachat-footer-section {
    border-bottom-left-radius: -4px;
    border-bottom-right-radius: -10px;
  }
  .ninjachat-footer-section .ninjachat-text-area-icons {
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 50px;
  }
  .ninjachat-footer-section textarea {
    box-sizing: border-box;
    padding: 0px 50px 0px 0px;
    width: 100%;
    height: 100%;
    font-family: "Poppins", sans-serif;
    font-size: 14px;
    font-weight: normal;
    line-height: 1.33;
    background-color: rgb(247 247 248);
    white-space: pre-wrap;
    overflow-wrap: break-word;
    border: 0;
  }
  .ninjachat-footer-section textarea:focus-visible {
    outline: none;
    border: 0;
  }
  button {
    background: none;
    border: unset;
  }
  .ninjachat-footer-section .ql-editor.ql-blank::before {
    font-style: normal;
    left: 0px !important;
  }
  .margin-zero p {
    margin: 0;
  }
  .bg-input-color::-webkit-scrollbar {
    width: 3px;
  }
  .bg-input-color::-webkit-scrollbar-thumb {
    background: #d2d2d2;
    border-radius: 5px;
  }
  .bg-input-color::-webkit-scrollbar-track {
    box-shadow: inset 20px 20px 20px #fff;
    border-radius: 0px;
  }
  .chat_icon .btn:focus,
  .btn.focus {
    outline: 0;
    box-shadow: 0 0 0 0.2rem rgb(0 123 255 / 0%);
  }
  .ninjachat-btn-light:hover {
    background-color: #e4e5ff !important;
  }
  .ninjachat-btn-light:focus,
  .ninjachat-btn-light.focus {
    box-shadow: 0 0 0 0.2rem rgb(130 138 145 / 0%) !important;
  }
  .ninjachat .ninjachat-items-center {
    border: 0px !important;
    padding: 0px !important;
    margin: 0px !important;
  }
  .ninjachat .chat-body-height::-webkit-scrollbar {
    width: 5px;
  }
  .ninjachat .chat-body-height::-webkit-scrollbar-thumb {
    background: #d2d2d2;
    border-radius: 5px;
  }
  .ninjachat .chat-body-height::-webkit-scrollbar-track {
    box-shadow: inset 20px 20px 20px #fff;
    border-radius: 0px;
  }
  .ninjachat .ninjachat-text-area-icons .bg-input-color {
    height: 40px !important;
    width: 100% !important;
    resize: none !important;
  }
  .ninjachat .ninjachat-footer-icons {
    display: inline !important;
  }
  .ninjachat .margin-zero {
    word-break: break-all !important;
  }
  .ninjachat .ninjachat-send-img {
    bottom: 13px !important;
  }
  .ninjachat .ninjachat-footer-section {
    padding: 15px 20px 0px !important;
  }
  .ninjachat-chat {
    right: 20px !important;
  }
</style>



