<template>
  <div class="ninjachat-header-profile-section" :style="UIPrefs.chat_header.css">
    <div class="ninjachat-profile-pic-section">
      <div class="ninjachat-profile-flex">
        <b-img v-if="!conversationHeader" :src="UIPrefs.chat_header.attr.gravatar" class="ninjachat-gravatar" />
        <div v-else @click="navigate" style="cursor:pointer;">
          <b-img src="https://infinity.500apps.com/img/ninjchat/chevron-left.svg" class="ninjachat-chevron-left" />
        </div>
        <div class="ninjachat-margin-left" style="cursor:pointer;" @click="minimize()">
          <div v-if="!conversationHeader">
            <div class="ninjachat-bot-name">
              {{ UIPrefs.chat_header.attr.widget_name }}
            </div>
            <div class="ninjachat-bot-description">
              {{ UIPrefs.chat_header.attr.widget_description }}
            </div>
          </div>
          <div v-else class="ninjachat-bot-conv-name">Conversation History</div>
        </div>
      </div>
      <div class="ninjachat-history-icon-bg" v-if="!conversationHeader">
        <button class="ninjachat-widget-history" aria-label="Open conversation history" @click="closeChat()">
          <b-img src="https://infinity.500apps.com/img/ninjachat/history-icon.svg" class="ninjachat-history-icon" />
        </button>
      </div>
    </div>
  </div>
</template>
<script>
  export default {
    name: "Header",
    components: {},
    props: {
      conversationHeader: Boolean,
    },
    data() {
      return {
        UIPrefs: window._NC.UIPrefs.prefs,
      };
    },
    mounted() {
      // Hide chat widget when user click on header
      document.addEventListener("click", (event) => {
        if (event.target.className.includes("profile")) return this.minimize();
      });
    },
    methods: {
      minimize() {
        this.$router.push({
          name: "icon",
          params: { active: false },
        });
        // To set chat status in vuex while chat end session
        window._NC.State.commit("setChatStatus", false);
      },

      // Navigate to active conversation view
      navigate() {
        window.refresh_loading = false;
        var result = window._NC.State.getters.getActiveConversation;
        // To set active conversation
        result.forEach((obj) => {
          window._NC.State.commit("setMessages", obj);
        });
        window._NC.State.commit("setActiveConversation", []);
        this.$router.push({
          name: "chat",
          params: { active: true },
        });
        // To set chat status in vuex while chat end session
        window._NC.State.commit("setChatStatus", true);
      },

      // Close chat by client
      async closeChat() {
        let listener = new window._NC.Chat.listener();
        listener.execute({ subject: "close_chat" });
      },
    },
  };
</script>



