<template>
  <div id="chatbot" class="ninjachat-chat" :style="UIPrefs.chat_width.css">
    <div class="ninjachat">
      <Header> </Header>
      <div id="ninjachat" class="chat-body-height ninjachat-bg-img-section" :style="UIPrefs.chat_body.css">
        <Messages></Messages>
      </div>
      <div>
        <InputEditor></InputEditor>
        <Footer></Footer>
      </div>
    </div>
  </div>
</template>
<script>
  import Header from "./Window/Header";
  import Footer from "./Window/Footer";
  import Messages from "./Window/Messages.vue";
  import InputEditor from "./Window/InputEditor.vue";

  export default {
    data() {
      return {
        UIPrefs: window._NC.UIPrefs.prefs,
      };
    },
    components: {
      Header,
      Footer,
      Messages,
      InputEditor,
    },

    mounted() {
      // Automatically scroll down in onload
      this.scrollToEnd();
    },
    methods: {
      // Automatically scroll down while updating conversation
      scrollToEnd() {
        setTimeout(() => {
          const container = document.getElementsByClassName("chat-body-height ninjachat-bg-img-section")[0];
          if (container) container.scrollTop = container.scrollHeight;
        }, 0);
      },
    },
  };
</script>



